.about-container{margin-top:20vh;margin:0 auto;margin-right:100px;margin-left:100px;margin-bottom:5vh}.about-container .terminal .terminal-header{height:28px;background-color:#e4e3e5;border-radius:10px 10px 0 0;display:flex;justify-content:flex-start;align-items:center}.about-container .terminal .terminal-header .header-button{width:12px;height:12px;border-radius:50%;margin-left:9px}.about-container .terminal .terminal-header .red{background-color:#f96256;border:solid 1px #f65549}.about-container .terminal .terminal-header .yellow{background-color:#fdbc3d;border:solid 1px #ffb524}.about-container .terminal .terminal-header .green{background-color:#33c948;border:solid 1px #2dbb41}.about-container .terminal .terminal-window{background:#34393f;border-radius:0 0 4px 4px;box-shadow:0 50px 100px rgba(50,50,93,.15),0 15px 35px rgba(50,50,93,.2),0 5px 15px rgba(0,0,0,.12);font-family:"Monaco",Consolas,"Lucida Console",monospace}.about-container .terminal .terminal-window a{color:#6dedf7;text-decoration:none}.about-container .terminal .terminal-window a:hover{color:#6dedf7}.about-container .terminal .terminal-window .statement:first-child{padding-top:35px}.about-container .terminal .terminal-window .statement:last-child{padding-bottom:60px}.about-container .terminal .terminal-window .statement{margin-bottom:25px;margin-left:35px;margin-right:35px;line-height:24px;text-align:left}.about-container .terminal .terminal-window .statement .input-statement{color:#f7f7f7}.about-container .terminal .terminal-window .statement .input-statement:before{content:"> "}.about-container .terminal .terminal-window .statement .return-statement{color:#98fb98}.about-container .terminal .terminal-window .statement span{background:rgba(247,247,247,.65);animation:caret 1s steps(1) infinite}@keyframes caret{50%{background-color:rgba(0,0,0,0)}}